<template>
  <div class="container">
    <form class="pt-3" @submit.prevent="onSubmit">
      <div class="form-group">
        <!-- ввод почты -->
        <label for="email"> Email </label>
        <input
          type="email"
          id="email"
          class="form-control"
          :class="{ 'is-invalid': $v.email.$error }"
          v-model="email"
          @blur="$v.email.$touch()"
        />
        <div class="invalid-feedback" v-if="!$v.email.required">
          Email field is required.
        </div>
        <div class="invalid-feedback" v-if="!$v.email.email">
          Please provide a valid email.
        </div>
        <div class="invalid-feedback" v-if="!$v.email.uniqEmail">
          This email is already exists
        </div>
      </div>
      <!-- ввод пароля -->
      <div class="form-group">
        <label for="password"> Password </label>
        <input
          type="password"
          id="password"
          class="form-control"
          :class="{ 'is-invalid': $v.password.$error }"
          v-model="password"
          @blur="$v.password.$touch()"
        />
        <div class="invalid-feedback" v-if="!$v.password.required">
          Password field is required.
        </div>
        <div class="invalid-feedback" v-if="!$v.password.minLength">
          Min lenght of password is {{ $v.password.$params.minLength.min }}. Now
          to os {{ password.lenght }}.
        </div>
      </div>
      <!-- проверка введённого пароля -->
      <div class="form-group">
        <label for="confirmPassword"> Confirm password </label>
        <input
          type="password"
          id="confirmPassword"
          class="form-control"
          :class="{ 'is-invalid': $v.confirmPassword.$error }"
          v-model="confirmPassword"
          @blur="$v.confirmPassword.$touch()"
        />
        <div class="invalid-feedback" v-if="!$v.confirmPassword.sameAs">
          Passwords should match.
        </div>
      </div>
         <!-- Кнопка отправки формы -->
      <button
        class="btn btn-success"
        type="submit"
        :disabled="$v.$invalid"
        @click="counter++"
      >
        Submit
      </button>
        <!-- Модальное окно с капчей -->
      <div
        class="modal modal-dialog-centered"
        tabindex="-1"
        role="dialog"
        id="staticBackdrop"
        v-if="counter === 3"
      >
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title" id="staticBackdropLabel">Warning!!!</h5>
            </div>
            <div class="modal-body">
              <p>
                You made a mistake with the password three times, please go
                through the captcha check.
              </p>
            </div>
            <div class="modal-footer">
              <button
                class="pt-3 btn btn-danger g-recaptcha"
                data-sitekey="reCAPTCHA_site_key"
                data-callback="onSubmitChapcha"
                data-action="submit"
                id="demo-form"
              >
                reChapcha
              </button>
            </div>
          </div>
        </div>
      </div>
    </form>
  </div>
</template>
<script>
//проверяем на обязательный ввод, корректность почты, мин длинна и коррекно ли вводят пароль
import { required, email, minLength, sameAs } from "vuelidate/lib/validators";

export default {
  data() {
    return {
      email: "",
      password: "",
      confirmPassword: "",
      counter: 0
    };
  },
  methods: {
    onSubmit() {
      console.log(this.email, this.password, this.confirmPassword);
    },
    onWorning() {
      let mail = []
      if(newEmail) {
        mail.push(newEmail)
        if(newEmail[mail]) {
          console.log("такая почта уже есть")
        }
      }
    },
    onSubmitChapcha(token) {
      document.getElementById("demo-form").submit();
    }
  },
  validations: {
    email: {
      required,
      email,
      uniqEmail: function(newEmail) {
        //тут мы идем на сервер и смотрим есть ли у нас такой эмеил
        //когда подлючу axios то будет список данных
        if (newEmail === "") return true;
        return new Promise((resolve, reject) => {
          const value = newEmail !== "test@mail.com";
          resolve(value);
        });
      }
    },
    password: {
      required,
      minLength: minLength(6)
    },
    confirmPassword: {
      sameAs: sameAs("password")
    }
  }
};
</script>

<style></style>



